// REM conversions
@function calcRem($num) {
    $stripped: $num / ($num * 0 + 1);
    $rems: $stripped / 16;
    @return #{$rems}rem;
}

@mixin font-size($size) {
    $stripped: $size / ($size * 0 + 1);
    font-size: #{$stripped}px;
    font-size: calcRem($stripped);
    line-height: calcRem($stripped * 1.5);
}

@mixin line-height($font-size, $multiplier) {
    $stripped: $font-size / ($font-size * 0 + 1);
    line-height: calcRem($stripped * $multiplier);
}

/* mixin to add vendor prefixes to transitions */
@mixin transition($args...) {
    -webkit-transition: $args;
    -moz-transition: $args;
    -ms-transition: $args;
    -o-transition: $args;
    transition: $args;
}

/* cross browser opacity */
@mixin opacity($opacity) {
    opacity: $opacity;
    $opacity-ie: $opacity * 100;
    filter: alpha(opacity=$opacity-ie); //IE8
}
