/* primary colors */
$white: #ffffff;
$black: #171717;
$brand-blue: #0079C2;
$light-gray: #F5F5F5;
$medium-gray: #CDCDCD;
$dark-gray: #666666;

/* brand colors */
$medium-salmon: #F6E8EC;
$dark-salmon: #985570;

$medium-twilight: #EBE7F2;
$dark-twilight: #5C4C79;

$medium-dusk: #E6E9F0;
$dark-dusk: #424B6C;

$medium-rain: #E0EDF3;
$dark-rain: #205F7C;

$medium-palm: #EBF0EA;
$dark-palm: #466E5A;

$medium-gold: #FBF0E7;
$dark-gold: #A76936;

$medium-sunset: #F8E8E2;
$dark-sunset: #745342;

$medium-quartz: #E9E8E8;
$dark-quartz: #0A3C53;

$primary-colors: (
    white: #ffffff,
    black: #171717,
    brand-blue: #0079C2,
    light-gray: #F5F5F5,
    medium-gray: #CDCDCD,
    dark-gray: #666666
);

$medium-colors: (
    medium-salmon: #F6E8EC,
    medium-twilight: #EBE7F2,
    medium-dusk: #E6E9F0,
    medium-rain: #E0EDF3,
    medium-palm: #EBF0EA,
    medium-gold: #FBF0E7,
    medium-sunset: #F8E8E2,
    medium-quartz: #E9E8E8
);

$dark-colors: (
    dark-salmon: #985570,
    dark-twilight: #5C4C79,
    dark-dusk: #424B6C,
    dark-rain: #205F7C,
    dark-palm: #466E5A,
    dark-gold: #A76936,
    dark-sunset: #745342,
    dark-quartz: #0A3C53
);

$nested-brand-colors: (
    salmon: (
        medium: #F6E8EC,
        dark: #985570
    ),
    twilight: (
        medium: #EBE7F2,
        dark: #5C4C79
    ),
    dusk: (
        medium: #E6E9F0,
        dark: #424B6C
    ),
    rain: (
        medium: #E0EDF3,
        dark: #205F7C,
    ),
    palm: (
        medium: #EBF0EA,
        dark: #466E5A,
    ),
    gold: (
        medium: #FBF0E7,
        dark: #A76936
    ),
    sunset: (
        medium: #F8E8E2,
        dark: #745342
    ),
    quartz: (
        medium: #E9E8E8,
        dark: #0A3C53
    )
);

$brand-colors: map-merge(
    $medium-colors,
    $dark-colors
);

$all-colors: map-merge(
    $primary-colors,
    $brand-colors
);
