// Variables & dependencies
@import 'variables/variables';

// for use on index and element pages
.fed-page-container {
    padding: calcRem(20px) calcRem(20px);
    max-width: 1280px;
    background-color: $white;
    margin: auto;
}

.fed-page-header {
    margin: calcRem(30px) 0;
}

.fed-page-title {
    margin: 0 0 calcRem(10px);
}

.fed-page-back {
    color: $brand-blue;
    display: inline-block;
}

.fed-section {
    background-color: $light-gray;
    padding: calcRem(20px);
    margin-bottom: calcRem(20px);
}

.-light {
    background-color: $white;
}

.fed-section-title {
    margin-bottom: calcRem(20px);
}

.fed-section-info {
    margin-bottom: calcRem(20px);
}

.fed-section-group {
    margin-bottom: calcRem(20px);
}

.fed-index-item {
    display: flex;
    background: $light-gray;
    color: $brand-blue;
    text-decoration: none;
    font-weight: 500;
    padding: calcRem(10px) calcRem(18px);
    margin-bottom: calcRem(10px);

    &:hover {
        background: darken($light-gray, 6%);
    }
}

.fed-element-group {
    margin-bottom: calcRem(50px);

    p {
        margin: calcRem(10px) 0;
    }
}

.fed-element-title {
    margin-top: calcRem(30px);
    margin-bottom: calcRem(10px);
}

pre {
    max-width: 100%;
    overflow-x: auto;
}

.font__section {
    &--alda {

        h3,
        p {
            font-family: $font-family-alda;
        }
    }

    &--effra {

        h3,
        p {
            font-family: $font-family-effra;
        }
    }
}

.fed-background-opaque {
    background-color: $brand-blue;
}

/* colors */

.fed-global_item {
    box-shadow: 0px 0px 10px rgba(51, 51, 51, 0.15);
    width: calcRem(150px);
    height: calcRem(150px);
    border-radius: 4px;

    .-top {
        border-radius: 4px 4px 0 0;
        height: 50%;
    }

    .-bottom {
        border-radius: 0 0 4px 4px;
        height: 50%;
        background: $white;

        p {
            font-size: 12px;
            font-family: $font-family-effra;
            font-weight: $font-weight-regular;
            text-align: center;
            padding-top: 20px;
        }
    }
}

.grid {
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: 100px 10px 100px 10px 100px;
    -ms-grid-rows: 100px 10px 100px;
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
}

.cmp-breaker {
    margin: 1rem 0;
}

error {
    color: red;
}

@each $all-color-name, $all-color in $all-colors {
    .-#{$all-color-name} {
        background: $all-color;
    }
}

/***** Classes added for typography page Start *****/

.test {
    background: $white;
}

.section-heading {
    color: $black;
    font-family: $font-family-effra;
    font-size: $font-size-tiny;
    font-weight: bold;
    letter-spacing: 3.5px;
    line-height: 21px;
}

.section-details {
    color: $dark-gray;
    font-family: $font-family-effra;
    font-size: 20px;
    line-height: 30px;
}

.cmp-hero__heading {
    padding: 67px 1.25rem !important;

    h1 {
        padding-right: 100px;
    }

    .section-heading,
    .section-details {
        color: $white;
    }
}

.cmp-title-banner {
    margin-top: 22px;
    margin-bottom: 22px;

    &__body {
        & ul li {
            list-style: none;
            color: #666666;
            font-family: $font-family-effra;
            font-size: 20px;
            line-height: 30px;
        }
    }
}

.cmp-hero__heading {
    @media (max-width: 767.98px) {
        .section-heading,
        .section-details {
            color: $black;
        }
    }
}

/***** Classes added for typography page End *****/

// typography for section specific pages - to be moved to component level

.cmp-hero__heading {
    background-color: $black;
    color: $white;
    padding: 1.25rem;
    h1 {
        @include font-size(48px);
    }
    @media (max-width: 767.98px) {
    //@include media-breakpoint-down(sm) {
        background-color: $white;
        color: $black;
        h1 {
            @include font-size(40px);
            @include line-height(48px, 1.2);
        }
    }
}

.cmp-category {
    h1 {
        line-height: calcRem(68px);
    }
    @media (max-width: 767.98px) {
    // @include media-breakpoint-down(sm) {
        h1 {
            @include font-size(58px);
        }
    }
}

.cmp-detail {
    h1 {
        @include font-size(58px);
        line-height: calcRem(68px);
    }
    @media (max-width: 767.98px) {
    //@include media-breakpoint-down(sm) {
        h1 {
            @include font-size(48px);
            @include line-height(48px, 1.25);
        }
    }
}

// use dark color map in variables/_colors for title banner color variations
@each $dark-color-name, $dark-color in $dark-colors {
    .cmp-title-banner__title--#{$dark-color-name} {
        h1, p {
            color: $dark-color;
        }
    }
}
// end typography for section specific pages
